/**
 * PREMIUM FEATURE: Twilio SMS Service
 *
 * Handles sending SMS reminders for invoice collections.
 * Requires paid subscription tier to use.
 *
 * Setup Instructions:
 * 1. Sign up for Twilio account at https://www.twilio.com
 * 2. Get Account SID and Auth Token from console
 * 3. Purchase a UK phone number with SMS capabilities
 * 4. Add to .env.local:
 *    TWILIO_ACCOUNT_SID=your_account_sid
 *    TWILIO_AUTH_TOKEN=your_auth_token
 *    TWILIO_PHONE_NUMBER=+44xxxxxxxxxx
 * 5. Install package: npm install twilio
 *
 * UK SMS Compliance:
 * - Must have explicit consent from recipient
 * - Must include opt-out instructions
 * - Must respect quiet hours (typically 21:00 - 08:00)
 * - Must not exceed rate limits
 *
 * Twilio UK Pricing (as of 2025):
 * - SMS to UK mobiles: ~£0.04 per message
 * - Phone number rental: ~£1/month
 */

import { logError, logExternalApiCall, logInfo, logWarn } from '@/utils/logger';
import { db, Timestamp } from '@/lib/firebase';
import twilio from 'twilio';

/**
 * Initialize Twilio client
 * Only runs in production or when explicitly enabled
 */
function getTwilioClient() {
  const accountSid = process.env.TWILIO_ACCOUNT_SID;
  const authToken = process.env.TWILIO_AUTH_TOKEN;

  if (!accountSid || !authToken) {
    throw new Error('Twilio credentials not configured');
  }

  return twilio(accountSid, authToken);
}

/**
 * SMS Template Types
 */
export type SMSTemplate = 'gentle_reminder' | 'urgent_reminder' | 'final_notice' | 'payment_link';

/**
 * Get SMS template text
 */
function getSMSTemplate(
  template: SMSTemplate,
  params: {
    invoiceReference: string;
    amount: number;
    dueDate: string;
    paymentLink?: string;
    businessName: string;
  }
): string {
  const { invoiceReference, amount, dueDate, paymentLink, businessName } = params;

  const templates = {
    gentle_reminder: `Hi, this is a friendly reminder from ${businessName} about invoice ${invoiceReference} for £${amount.toFixed(2)}, due ${dueDate}. ${paymentLink ? `Pay now: ${paymentLink}` : 'Please arrange payment at your earliest convenience.'} Reply STOP to opt out.`,

    urgent_reminder: `URGENT: Invoice ${invoiceReference} for £${amount.toFixed(2)} is now overdue (due ${dueDate}). ${paymentLink ? `Pay immediately: ${paymentLink}` : 'Please contact us urgently.'} - ${businessName}. Reply STOP to opt out.`,

    final_notice: `FINAL NOTICE: Invoice ${invoiceReference} for £${amount.toFixed(2)} remains unpaid. This is your last reminder before further action. ${paymentLink ? `Pay now: ${paymentLink}` : 'Contact us immediately.'} - ${businessName}. Reply STOP to opt out.`,

    payment_link: `${businessName}: Pay invoice ${invoiceReference} (£${amount.toFixed(2)}) securely: ${paymentLink}. Reply STOP to opt out.`,
  };

  return templates[template];
}

/**
 * Check if within UK quiet hours
 */
function isQuietHours(): boolean {
  const now = new Date();
  const hour = now.getHours();

  // Quiet hours: 21:00 (9 PM) to 08:00 (8 AM)
  return hour >= 21 || hour < 8;
}

/**
 * Send SMS reminder for invoice collection
 *
 * @param params SMS parameters
 * @returns Twilio message SID or null if failed
 */
export async function sendCollectionSMS(params: {
  recipientPhone: string; // E.164 format: +44xxxxxxxxxx
  invoiceReference: string;
  amount: number;
  dueDate: string;
  template: SMSTemplate;
  paymentLink?: string;
  businessName: string;
  // Internal tracking
  invoiceId: string;
  freelancerId: string;
}): Promise<{
  success: boolean;
  messageSid?: string;
  error?: string;
  status?: 'queued' | 'sent' | 'failed';
}> {
  const startTime = Date.now();

  try {
    // 1. Validate phone number format
    if (!params.recipientPhone.startsWith('+44')) {
      throw new Error('Only UK phone numbers supported (+44)');
    }

    // 2. Check if user has opted out (PECR compliance)
    const userSnapshot = await db
      .collection('users')
      .where('phoneNumber', '==', params.recipientPhone)
      .limit(1)
      .get();

    if (!userSnapshot.empty) {
      const user = userSnapshot.docs[0].data();
      if (user.collectionsConsent?.smsOptedOut) {
        logWarn('SMS not sent: User has opted out', {
          phone: params.recipientPhone,
          optOutDate: user.collectionsConsent.smsOptOutDate,
        });
        return {
          success: false,
          error: 'User has opted out of SMS communications',
        };
      }
    }

    // 3. Check quiet hours
    if (isQuietHours()) {
      logError('SMS not sent: Quiet hours (21:00-08:00)', new Error('Quiet hours'), {
        invoiceId: params.invoiceId,
      });
      return {
        success: false,
        error: 'Cannot send SMS during quiet hours (21:00-08:00)',
      };
    }

    // 4. Get SMS text
    const messageBody = getSMSTemplate(params.template, {
      invoiceReference: params.invoiceReference,
      amount: params.amount,
      dueDate: params.dueDate,
      paymentLink: params.paymentLink,
      businessName: params.businessName,
    });

    // 4. Send via Twilio
    const client = getTwilioClient();
    const message = await client.messages.create({
      body: messageBody,
      from: process.env.TWILIO_PHONE_NUMBER,
      to: params.recipientPhone,
      // Status callback for delivery tracking
      statusCallback: `${process.env.NEXT_PUBLIC_APP_URL}/api/webhooks/twilio/sms-status`,
    });

    const duration = Date.now() - startTime;
    logExternalApiCall('Twilio SMS', 'send', duration);

    return {
      success: true,
      messageSid: message.sid,
      status: message.status as 'queued' | 'sent' | 'failed',
    };

  } catch (error) {
    const duration = Date.now() - startTime;
    logExternalApiCall('Twilio SMS', 'send', duration, error as Error);

    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Get SMS delivery status from Twilio
 *
 * @param messageSid Twilio message SID
 * @returns Message status
 */
export async function getSMSStatus(messageSid: string): Promise<{
  status: 'queued' | 'sending' | 'sent' | 'delivered' | 'undelivered' | 'failed';
  errorCode?: string;
  errorMessage?: string;
}> {
  try {
    const client = getTwilioClient();
    const message = await client.messages(messageSid).fetch();

    return {
      status: message.status as any,
      errorCode: message.errorCode?.toString(),
      errorMessage: message.errorMessage || undefined,
    };
  } catch (error) {
    logError('Failed to fetch SMS status', error);
    return {
      status: 'failed',
      errorMessage: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Handle SMS opt-out (STOP replies)
 *
 * This should be called from the Twilio webhook when a user replies STOP
 * UK law (PECR) requires honoring opt-out requests immediately
 *
 * PECR Requirement: Must stop sending marketing messages within 28 days
 * Best practice: Honor immediately
 */
export async function handleSMSOptOut(phoneNumber: string): Promise<{
  success: boolean;
  usersUpdated: number;
  error?: string;
}> {
  try {
    // Normalize phone number to E.164 format
    const normalizedPhone = formatUKPhone(phoneNumber) || phoneNumber;

    logInfo('Processing SMS opt-out request', { phoneNumber: normalizedPhone });

    // Find all users with this phone number (could be multiple if phone shared)
    const usersSnapshot = await db
      .collection('users')
      .where('phoneNumber', '==', normalizedPhone)
      .get();

    if (usersSnapshot.empty) {
      logWarn('No users found for opt-out phone number', { phoneNumber: normalizedPhone });
      return {
        success: true,
        usersUpdated: 0,
      };
    }

    // Update all matching users
    const batch = db.batch();
    const now = Timestamp.now();

    usersSnapshot.docs.forEach((doc) => {
      batch.update(doc.ref, {
        'collectionsConsent.smsConsent': false,
        'collectionsConsent.smsOptedOut': true,
        'collectionsConsent.smsOptOutDate': now,
        updatedAt: now,
      });
    });

    await batch.commit();

    logInfo('SMS opt-out processed successfully', {
      phoneNumber: normalizedPhone,
      usersUpdated: usersSnapshot.size,
    });

    return {
      success: true,
      usersUpdated: usersSnapshot.size,
    };
  } catch (error: any) {
    logError('Failed to process SMS opt-out', error, { phoneNumber });
    return {
      success: false,
      usersUpdated: 0,
      error: error.message,
    };
  }
}

/**
 * Validate phone number format
 */
export function isValidUKPhone(phone: string): boolean {
  // Basic UK phone validation (E.164 format)
  const ukPhoneRegex = /^\+44[1-9]\d{9,10}$/;
  return ukPhoneRegex.test(phone);
}

/**
 * Format UK phone to E.164
 * Converts various UK formats to +44xxxxxxxxxx
 */
export function formatUKPhone(phone: string): string | null {
  // Remove all spaces, dashes, parentheses
  const cleaned = phone.replace(/[\s\-\(\)]/g, '');

  // Convert 07xxx to +447xxx
  if (cleaned.startsWith('07')) {
    return '+44' + cleaned.substring(1);
  }

  // Convert 447xxx to +447xxx
  if (cleaned.startsWith('447')) {
    return '+' + cleaned;
  }

  // Already in E.164 format
  if (cleaned.startsWith('+44')) {
    return cleaned;
  }

  return null;
}
